@font-face {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 400;
    src: url('/static/fonts/raleway/raleway-v14-latin-regular.eot');
    /* IE9 Compat Modes */
    src: local('Raleway'), local('Raleway-Regular'),
        url('/static/fonts/raleway/raleway-v14-latin-regular.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */
        url('/static/fonts/raleway/raleway-v14-latin-regular.woff2') format('woff2'),
        /* Super Modern Browsers */
        url('/static/fonts/raleway/raleway-v14-latin-regular.woff') format('woff'),
        /* Modern Browsers */
        url('/static/fonts/raleway/raleway-v14-latin-regular.ttf') format('truetype'),
        /* Safari, Android, iOS */
        url('/static/fonts/raleway/raleway-v14-latin-regular.svg#Raleway') format('svg');
    /* Legacy iOS */
}

